@import url(https://fonts.googleapis.com/css2?family=Passion+One:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header-wrapper {
    background: linear-gradient(90deg, rgba(31,29,30,1) 0%, rgba(48,83,125,1) 100%);
    box-shadow: 0 0px 20px 5px #000;
}
.header {
    height: 50px;
    display: flex;
    align-items: flex-end;
    position: relative;
}
.header-link {
    /* margin-right: 20px; */
    text-transform: uppercase;
    color: darkorange;
    text-decoration: none;
    font-size: 18px;
    padding: 5px 20px;
    display: flex;
    align-items: center;
    transition: .3s;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
}
.header-link__active {
    color: #000;
    background: #ffffff;
}
.banner {
    overflow: hidden;
    position: relative;
    height: 300px;
    background: url(/router-app/static/media/banner.1dce28cf.jpg) center center no-repeat;
    background-size: cover;
    border-radius: 45px;
    box-shadow: 0 0 10px 1px #000;
}
.contacts-wrapper {
    width: 600px;
    margin-right: auto;
    margin-left: auto;
    background: linear-gradient(90deg, rgba(31,29,30,1) 0%, rgba(48,83,125,1) 100%);
    padding: 10px 15px;
    border-radius: 10px;
    position: relative;
}
.contacts-conditions {
    display: flex;

}
.contacts-conditions label {
    display: flex;
    align-items: center;
    font-size: 10px;
}
.contacts-conditions label img {
    width: 15px;
}
.contacts-search {
    display: flex;
    margin-bottom: 5px;
    justify-content: space-between;
    color: rgb(165, 181, 187);
    opacity: 0.8;
}
.contacts-search span {
    margin-left: auto;
}
.contacts-items {
    height: calc(100vh - 210px);
    overflow-y: scroll;
    padding-right: 5px;
    scrollbar-width: 3px;
    scrollbar-color: rgb(211, 170, 162) rgb(233, 228, 220);
    -ms-overflow-style: none;
    scrollbar-width: none;
}
.contacts-items::-webkit-scrollbar{
    width: 3px;
    background: rgb(233, 228, 220);
}
.contacts-items::-webkit-scrollbar-thumb {
    background: rgb(211, 170, 162);
}
.contacts-search input[type="text"] {
    width: 60%;
    margin-left: 10px;
    border: none;
    background: none;
    outline: none;
    border-radius: 15px;
    padding: 5px 15px;
    box-sizing: border-box;
    color: white;
}
.contacts-search input:focus {
    box-shadow: 0 0 5px  rgba(160, 81, 45, 0.192);
}
.contacts-items a {
    text-decoration: none;
}
.contact {
    display: grid;
    grid-template-columns: 2fr 1fr 100px;
    align-items: center;
    background: rgba(255, 255, 255, 0.719);
    padding: 10px 25px;
    border-radius: 15px;
    font-size: 16px;
    color: rgb(39, 34, 71);
    margin: 4px 0;
    cursor: pointer;
    text-decoration: none;
    height: 28px;
}
.contact:hover {
    box-shadow: 0 0 3px 0px #000 inset;
}
.contact-name {
    color: rgb(29, 48, 100);
    font-weight: bold;
    margin-right: 10px;
}
.contact-gender {
    text-align: right;
}
.contacts-count {
    font-size: 10px;
    text-align: right;
    margin-top: 4px;
}

.full-contact-wrapper {
    padding: 20px 30px;
    border-radius: 20px;
    background: rgba(86, 131, 86);
    color: white;
    max-width: 50%;
    position: relative;
    transition: .4s;
    opacity: 0;
    -webkit-animation: opac .3s linear forwards;
            animation: opac .3s linear forwards;
}
@-webkit-keyframes opac {
    0% {opacity: 0;}
    100% {opacity: 1;}
}
@keyframes opac {
    0% {opacity: 0;}
    100% {opacity: 1;}
}
.full-contact-wrapper::after {
    position: absolute;
    content: 'x';
    top: -10px;
    right: -10px;
    pointer-events: none;
}
.full-contact-header {
    color: #000;
    font-size: 20px;
    display: flex;
    align-items: center;
    font-weight: bold;
    letter-spacing: 1px;
}
.full-contact-gender {
    margin-left: auto;
}
.full-contact-about {
    margin-top: 10px;
    font-size: 10px;
    line-height: 2;
}
.page {
    z-index: 0;
    height: calc(100vh - 110px);
    padding: 30px 20px 30px;
    transition: .5s;
    overflow: hidden;
}
.page::after {
    position: fixed;
    z-index: 10;
    content: '@space-for-people';
    font-size: 10px;
    width: calc(100vw + 20px);
    height: 30px;
    background: linear-gradient(90deg, rgba(48,83,125,1)  0%, rgba(31,29,30,1) 100%);
    bottom: 0px;
    left: -20px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.page-home {
    background: rgba(4, 51, 38, 0.8);
}
.page-posts {
    background: rgba(65, 34, 37, 0.8);
}
.page-fotos {
    background: rgba(82, 90, 62, 0.8);
}
.page-contacts {
    background: rgba(86, 131, 86, 0.8);
}
.page-error {
    background: rgba(0, 0, 0, 0.8);
    color: #aa9494;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    font-family: monospace;
}
.home-text {
    font-size: 40px;
    text-align: center;
    color: rgb(50, 58, 58);
    font-family: 'Passion One', cursive;
}
.posts-wrapper {
    height: calc(100vh - 110px);
    width: 100%;
    overflow-y: scroll;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    position: relative;
}
.posts-wrapper::-webkit-scrollbar {
    width: 0px;
}
.post {
    width: 600px;
    height: auto;
    background: #15202b;
    color: #fff;
    padding: 10px 20px;
    border-radius: 30px;
    margin: 10px;
    position: relative;
    box-shadow: 0 0 10px 1px #000;
}
.post-header {
    display: flex;
}
.post-avatar {
    width: 60px;
    height: 60px;
    margin-right: 20px;
    overflow: hidden;
    border-radius: 50%;
}
.post-avatar img {
    width: 100%;
    height: 100%;
}
.post-text {
    margin-top: 5px;
}
.post-username-name {
    font-weight: bold;
    margin-right: 10px;
    
}
.post-username-status img {
    width: 20px;
    position: relative;
    top: 3px;
    margin-right: 10px;
}
.post-username-nickname {
    opacity: 0.6;
    color: rgb(82, 161, 135);
}
.post-username-date {
    color: rgb(82, 161, 135);
    opacity: 0.6;
}
.post-main {
    margin-top: 5px;
    display: flex;
    justify-content: flex-end;
}
.post-main img {
    max-width: 85%;
    margin-left: auto;
    border-radius: 30px;
}
.post-footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-left: 100px;
    margin-top: 10px;
}
.post-footer span {
    display: flex;
    align-items: center;
    color: #52a187;
    text-decoration: none;
}
.post-footer span:hover  {
    opacity: 0.7;
}
.post-footer span img {
    width: 20px;
    margin-right: 10px;
}
.fotos-wrapper {
    height: calc(100vh - 110px);
    width: 100%;
    overflow-y: scroll;
    position: relative;
    display: flex;
    flex-wrap: wrap;
}
.fotos-wrapper::-webkit-scrollbar {
    width: 0px;
}
.foto-wrapper {
    width: 24.5%;
    height: 285px;
    position: relative;
    border-radius: 5px;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    cursor: pointer;
    margin: 1px;
    box-shadow: 0 0 10px 1px #000;
    transition: .3s;
}
.foto-wrapper:hover {
    box-shadow: 0 0 10px 1px rgb(146, 146, 146);
    transition: .3s;
}
.foto-wrapper:last-child {
    margin-bottom: 10px;
}
.full-foto-wrapper {
    z-index: 1000;
    background-color: rgba(0, 0, 0, 0.6);
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: .4s;
}
.full-foto {
    transition: .4s;
    border-radius: 25px;
    max-height: 90%;
    opacity: 0;
    -webkit-animation: opac .3s linear forwards;
            animation: opac .3s linear forwards;
    -webkit-animation-delay: .3s;
            animation-delay: .3s;
}
@-webkit-keyframes opac {
    0% {opacity: 0;}
    100% {opacity: 1;}
}
@keyframes opac {
    0% {opacity: 0;}
    100% {opacity: 1;}
}
.container {
  max-width: 1140px;
  margin: 0 auto;
}
